@import "../../../styles/vars";
@import "../../../styles/mixins";

$sm-tab-size: 890px;

.footer {
  border-top: 1px solid $color-white;
  margin-top: 120px;

  @include xxlScreenMedia {
    margin-top: 100px;
  }

  @include lgScreenMedia {
    margin-top: 80px;
  }

  @include smScreenMedia {
    margin-top: 60px;
  }

  &__mainBlock {
    @include flexCol(space-between, flex-start);
    padding-top: 120px;
    padding-bottom: 120px;

    @include xxlScreenMedia {
      padding-top: 100px;
      padding-bottom: 100px;
    }

    @include xlScreenMedia {
      padding-top: 81px;
      padding-bottom: 44px;
    }

    @include lgScreenMedia {
      padding-top: 34px;
    }

    @media (max-width: $sm-tab-size) {
      padding-bottom: 0;
      justify-content: center;
    }

    @include smScreenMedia {
      padding-bottom: 19px;
      margin-top: 60px;
    }
  }

  &__logoBlock {
    @include flexCol();
    flex-direction: column;
  }


  &__logoImg {
    max-width: 300px;
    width: 100%;
    margin-bottom: 60px;

    @include xxlScreenMedia {
      max-width: 234px;
      margin-bottom: 40px;
    }

    @include xlScreenMedia {
      max-width: 183px;
      margin-bottom: 50px;
    }

    @include lgScreenMedia {
      max-width: 130px;
      margin-bottom: 25px;
    }
  }

  &__iconsBlock {
    @include flexCol(space-between);
    width: 100%;

    @include lgScreenMedia {
      justify-content: center;
      gap: 8px;
    }
  }

  &__iconsLink {
      svg {
        fill: $color-white;
        stroke: transparent;

        &:hover {
          fill: $color-light-blue;
        }
      }

  }

  &__iconsImg {
    transition: $transition;
    width: 31px;
    height: 31px;

    @include xlScreenMedia {
      width: 24px;
      height: 24px;
    }
  }

  &__linksBlock {
    @media (max-width: $sm-tab-size) {
      display: none;
    }
  }

  &__linksBlockTitle {
    @include setFont(18px, 500);
    color: $color-light-blue;
    margin-bottom: 20px;

    @include xlScreenMedia {
      margin-bottom: 12px;
    }

    @include lgScreenMedia {
      font-size: 16px;
      margin-bottom: 23px;
    }
  }

  &__linksBlockLink {
    @include setFont(16px, 300);
    color: $color-white;
    transition: $transition;
    text-decoration: none;
    display: block;
    cursor: pointer;
    @include flexCol(flex-start);

    @include xlScreenMedia {
      font-size: 14px;
    }

    &:not(:last-child) {
      margin-bottom: 15px;

      @include xxlScreenMedia {
        margin-bottom: 10px;
      }

      @include xlScreenMedia {
        margin-bottom: 12px;
      }

      @include lgScreenMedia {
        margin-bottom: 7px;
      }
    }

    &:hover,&_active {
      color: $color-light-blue;
      text-decoration: underline;
    }

    &:hover svg {
      stroke: $color-light-blue;
    }
  }

  &__linksBlockIcon {
    @include svg(17px, transparent, 17px, $color-white);
    margin-right: 12px;

    @include xlScreenMedia {
      width: 15px;
      height: 15px;
    }
  }

  &__subscribeBlock {
    &_desk {
      @include lgScreenMedia {
        display: none;
      }
    }

    &_tab {
      display: none;
      margin-top: 20px;

      @include lgScreenMedia {
        display: block;
      }
    }
  }

  &__accordionBlock {
    display: none;
    margin-top: 8px;

    @media (max-width: $sm-tab-size) {
      @include flexCol(flex-start, flex-start);
      flex-direction: column;
      gap: 8px;
    }
  }

  &__accordionItems {
    padding: 8px 0;
    border-bottom: 1px solid rgba($color-white, 0.4);
    width: 100%;
  }

  &__accordionTitle {
    cursor: pointer;
    @include flexCol(space-between);
  }

  &__accordionTitleText {
    @include setFont(18px, 500);
    color: $color-white;

    @include lgScreenMedia {
      font-size: 16px;
    }
  }

  &__accordionTitleIcon {
    display: block;
    @include svg(13px, $color-white);

    &_active {
      transform: rotate(180deg);
    }
  }

  &__accordionContent {
    overflow: hidden;
  }

  &__accordionLink {
    &:first-child {
      margin-top: 12px;
    }
    &:last-child {
      margin-bottom: 4px;
    }
  }

  &__copyrightBlock {
    border-top: 1px solid $color-white;
    padding: 12px 0 15px;

    @media (max-width: $sm-tab-size) {
      margin-top: 44px;
      padding-bottom: 10px;
    }

    @include smScreenMedia {
      margin-top: 35px;
    }
  }

  &__copyrightBlock__text {
    @include setFont(16px,300);
    color: $color-white;
    text-align: center;
  }
}