@import "../../../styles/vars";
@import "../../../styles/mixins";


.subscribeForm {
  @include flexCol();
  background: $color-white;
  padding: 2px 3px 2px 0;
  border-radius: 4px;
  position: relative;
  width: 100%;

  &__input {
    width: 100%;
    border: none;
    outline: none;
    height: 100%;
    padding: 0 6px 0 9px;
    @include setFont(18px, 300);

    @include xxlScreenMedia {
      font-size: 16px;
    }

    @include xlScreenMedia {
      font-size: 14px;
    }

    ::placeholder {
      color: $color-black;
      opacity: 0.4;
    }

    &:-webkit-autofill {
      box-shadow: 0 0 0px 1000px $color-white inset;
    }
  }

  &__btn {
    cursor: pointer;
    border: none;
    background: $color-light-blue;
    color: $color-white;
    min-width: 148px;
    white-space: nowrap;
    text-align: center;
    padding: 14px 8px 13px;
    border-radius: 4px;
    transition: $transition;
    @include setFont(18px, 500);

    &:hover,
    &:active {
      box-shadow: inset 0 0 0 2px $color-white;
    }
  }

  &__btn:not(&__popupBtn) {
    @include xxlScreenMedia {
      min-width: 113px;
    }

    @include xlScreenMedia {
      min-width: 92px;
      padding-top: 11px;
      padding-bottom: 10px;
      font-size: 16px;
    }

    @include xlScreenMedia {
      min-width: 92px;
      padding-top: 10px;
      padding-bottom: 9px;
    }
  }

  &__popupBtn {
    padding-top: 15px;
    padding-bottom: 18px;
    font-size: 20px;

    @include xlScreenMedia {
      font-size: 18px;
    }

    @include lgScreenMedia {
      font-size: 16px;
      min-width: 130px;
    }
    @include smScreenMedia {
      font-size: 14px;
      min-width: 92px;
      padding-top: 7px;
      padding-bottom: 9px;
    }

  }

  &__messageText {
    @include setFont(12px,300);
    position: absolute;
    left: 1px;
    z-index: -1;
    bottom: 0px;
    transition: $transition;

    @include lgScreenMedia {
      font-size: 10px;
    }

    &_error {
    color: $color-light-red;
    }

    &_success {
      color: $color-green;
    }

    &_visible {
      bottom: -14px;
      z-index: 1;
    }
  }

  &__popupMessageText {
    @include setFont(14px,400);
    position: absolute;
    left: 1px;
    z-index: -1;
    bottom: 0px;
    transition: $transition;
    color: $color-black;
    opacity: .8;

    &_visible {
      bottom: -21px;
      z-index: 1;
    }
  }
}
