@import "../../../../styles/vars";
@import "../../../../styles/mixins";

.footerSubscribeBlock {
  max-width: 406px;
  width: 100%;

  @include xxlScreenMedia {
    max-width: 315px;
  }

  @include xlScreenMedia {
    max-width: 265px;
  }


  &__form {
    margin-bottom: 25px;

    @include xxlScreenMedia {
      margin-bottom: 23px;
    }

    @include xlScreenMedia {
      margin-bottom: 28px;
    }

    @include lgScreenMedia {
      margin-bottom: 20px;
    }
  }



  &__text {
    @include setFont(16px, 300);
    color: $color-white;

    @include xlScreenMedia {
      font-size: 14px;
    }
  }
}